/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import CheckmarkBlack from '../Images/CheckmarkBlack.view.js';
import CheckmarkWhite from '../Images/CheckmarkWhite.view.js';
import React from 'react';
import { css } from 'emotion';

const MenuItem1 = css({
  label: 'MenuItem1',
  flexDirection: 'row',
  fontFamily: 'LeviWindows',
  fontSize: 34,
  lineHeight: '24px',
  backgroundColor: '#C1C1C3',
  color: '#000',
  height: 34,
  paddingLeft: 32,
  paddingBottom: 8,
  borderTopColor: '#fff',
  borderTopStyle: 'solid',
  borderBottomColor: '#939493',
  borderBottomStyle: 'solid',
  '&:hover': {
    backgroundColor: '#0016c0',
    color: '#fff',
  },
  borderTopWidth: 'var(--borderTopWidth)',
  borderBottomWidth: 'var(--borderBottomWidth)',
});
const Horizontal = css({ label: 'Horizontal', flexDirection: 'row' });
const BlackWrapper = css({
  label: 'BlackWrapper',
  flexDirection: 'row',
  position: 'absolute',
  left: '-24px',
  top: '12px',
  zIndex: 1,
  [`.${MenuItem1}:hover &, .${MenuItem1}.hover &`]: {
    zIndex: -1,
  },
});
const WhiteWrapper = css({
  label: 'WhiteWrapper',
  flexDirection: 'row',
  position: 'absolute',
  left: '-24px',
  top: '12px',
  zIndex: -1,
  [`.${MenuItem1}:hover &, .${MenuItem1}.hover &`]: {
    zIndex: 1,
  },
});

const MenuItem = props => {
  return (
    <button
      data-test-id={`${props['data-test-id'] || 'MenuItem'}|${
        props.hasTopBorder
          ? 'hasTopBorder|'
          : props.hasBottomBorder
            ? 'hasBottomBorder|'
            : ''
      }`}
      onClick={props.onClick}
      name={props.name}
      style={{
        '--borderTopWidth': `${props.hasTopBorder ? 1 : 0}px`,
        '--borderBottomWidth': `${props.hasBottomBorder ? 2 : 0}px`,
      }}
      className={`views-block ${MenuItem1}`}
    >
      {props.isSelected ? (
        <div
          data-test-id={`MenuItem.Horizontal|`}
          className={`views-block ${Horizontal}`}
        >
          <div
            data-test-id={`MenuItem.BlackWrapper|`}
            className={`views-block ${BlackWrapper}`}
          >
            <CheckmarkBlack
              data-test-id={`MenuItem.CheckmarkBlack|`}
              width={12}
              height={12}
              className="views-block"
            />
          </div>
          <div
            data-test-id={`MenuItem.WhiteWrapper|`}
            className={`views-block ${WhiteWrapper}`}
          >
            <CheckmarkWhite
              data-test-id={`MenuItem.CheckmarkWhite|`}
              width={12}
              height={12}
              className="views-block"
            />
          </div>
        </div>
      ) : null}
      <span data-test-id={`MenuItem.Text|`} className="views-block">
        {props.title}
      </span>
      {props.children}
    </button>
  );
};
MenuItem.defaultProps = { title: '' };
export default MenuItem;
