/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import React from 'react';
import Screensaver from './Screensaver.view.js';
import StandardModal from '../Modals/StandardModal.view.logic';
import VideoModal from '../Modals/VideoModal.view.js';
import Window from './Window.view.logic';
import { css } from 'emotion';

const App1 = css({
  label: 'App1',
  alignItems: 'center',
  flexGrow: 1,
  flexShrink: 1,
  flexBasis: 'auto',
  justifyContent: 'center',
  backgroundColor: '#008183',
});
const ModalBackground = css({
  label: 'ModalBackground',
  position: 'absolute',
  top: 0,
  bottom: 0,
  left: 0,
  right: 0,
});

const App = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'App'}|`}
      className={`views-block ${App1}`}
    >
      <Window
        data-test-id={`App.Window|`}
        showMenu={props.showMenu}
        className="views-block"
      />
      {props.preventBgClick ? (
        <div
          data-test-id={`App.ModalBackground|`}
          className={`views-block ${ModalBackground}`}
        />
      ) : null}
      {props.showVideo ? (
        <VideoModal
          data-test-id={`App.VideoModal|`}
          onClick={props.toggleItem}
          className="views-block"
        />
      ) : null}
      {props.showHelp ? (
        <StandardModal
          data-test-id={`App.StandardModal|`}
          title="Error"
          mainMessage="You are beyond help."
          isError={true}
          className="views-block"
        />
      ) : null}
      {props.showTimes ? (
        <StandardModal
          data-test-id={`App.StandardModal:1|`}
          title="Best times"
          mainMessage="At your age don't you think you should have more worthwhile aspirations than getting on a Minesweeper leaderboard?"
          secondaryMessage="Pathetic."
          isWarning={true}
          className="views-block"
        />
      ) : null}
      {props.showAbout ? (
        <StandardModal
          data-test-id={`App.StandardModal:2|`}
          title="About"
          mainMessage="Made with Views."
          secondaryMessage="You can check out the code at github.com/amymc/minesweeper."
          isAbout={true}
          className="views-block"
        />
      ) : null}
      {props.showScreensaver ? (
        <Screensaver
          data-test-id={`App.Screensaver|`}
          className="views-block"
        />
      ) : null}
      {props.children}
    </div>
  );
};

export default App;
