/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import Cool from '../Images/Cool.view.js';
import Dead from '../Images/Dead.view.js';
import Ohhh from '../Images/Ohhh.view.js';
import React from 'react';
import Smile from '../Images/Smile.view.js';
import { css } from 'emotion';

const Status1 = css({
  label: 'Status1',
  flexDirection: 'row',
  justifyContent: 'center',
  width: 41,
  height: 41,
  marginLeft: 10,
  marginRight: 10,
  borderTopLeftRadius: 2,
  borderTopRightRadius: 2,
  borderBottomLeftRadius: 2,
  borderBottomRightRadius: 2,
  borderStyle: 'solid',
  borderWidth: 3,
  alignItems: 'center',
  borderTopColor: 'var(--borderTopColor)',
  borderLeftColor: 'var(--borderLeftColor)',
  borderBottomColor: 'var(--borderBottomColor)',
  borderRightColor: 'var(--borderRightColor)',
});
const Face = css({
  label: 'Face',
  flexDirection: 'row',
  width: 30,
  height: 30,
  top: 1,
  alignSelf: 'center',
});

const Status = props => {
  return (
    <button
      data-test-id={`${props['data-test-id'] || 'Status'}|${
        props.isPressed ? 'isPressed|' : ''
      }`}
      onClick={props.onClick}
      onMouseDown={props.toggleState}
      onMouseUp={props.toggleState}
      style={{
        '--borderTopColor': `${props.isPressed ? '#1A161A' : '#939493'}`,
        '--borderLeftColor': `${props.isPressed ? '#1A161A' : '#939493'}`,
        '--borderBottomColor': `${props.isPressed ? '#fff' : '#1A161A'}`,
        '--borderRightColor': `${props.isPressed ? '#fff' : '#1A161A'}`,
      }}
      className={`views-block ${Status1}`}
    >
      <div data-test-id={`Status.Face|`} className={`views-block ${Face}`}>
        {props.isHappy ? (
          <Smile data-test-id={`Status.Smile|`} className="views-block" />
        ) : null}
        {props.isScared ? (
          <Ohhh data-test-id={`Status.Ohhh|`} className="views-block" />
        ) : null}
        {props.isDead ? (
          <Dead data-test-id={`Status.Dead|`} className="views-block" />
        ) : null}
        {props.isCool ? (
          <Cool data-test-id={`Status.Cool|`} className="views-block" />
        ) : null}
      </div>
      {props.children}
    </button>
  );
};

export default Status;
