/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import BoardColumn from './BoardColumn.view.js';
import React from 'react';
import { css } from 'emotion';

const Board1 = css({
  label: 'Board1',
  flexDirection: 'row',
  backgroundColor: '#C3BFC2',
  borderTopColor: '#7E7D80',
  borderTopWidth: 4,
  borderTopStyle: 'solid',
  borderLeftColor: '#7E7D80',
  borderLeftWidth: 4,
  borderLeftStyle: 'solid',
  borderRightColor: '#FEFCFF',
  borderRightWidth: 4,
  borderRightStyle: 'solid',
  borderBottomColor: '#FEFCFF',
  borderBottomWidth: 4,
  borderBottomStyle: 'solid',
});

const Board = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'Board'}|`}
      pass="column"
      className={`views-block ${Board1}`}
    >
      {Array.isArray(props.grid) &&
        props.grid.map((column, index) => (
          <BoardColumn
            index={index}
            column={column}
            key={index}
            data-test-id={`Board.BoardColumn|`}
            reveal={props.reveal}
            className="views-block"
          />
        ))}
      {props.children}
    </div>
  );
};

export default Board;
