/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import Button from '../Buttons/Button.view.js';
import ErrorIcon from '../Images/ErrorIcon.view.js';
import MessageWrapper from './MessageWrapper.view.js';
import React from 'react';
import TitleBar from '../Main/TitleBar.view.js';
import Views from '../Images/Views.view.js';
import WarningIcon from '../Images/WarningIcon.view.js';
import { css } from 'emotion';

const StandardModal1 = css({
  label: 'StandardModal1',
  flexDirection: 'column',
  position: 'absolute',
  width: 450,
  backgroundColor: '#C1C1C3',
  flexShrink: 1,
  borderWidth: 2,
  borderStyle: 'solid',
  borderColor: '#000',
  paddingTop: 2,
  paddingRight: 2,
  paddingBottom: 2,
  paddingLeft: 2,
  fontFamily: 'Overpass, sans-serif',
});
const OuterWrapper = css({
  label: 'OuterWrapper',
  flexDirection: 'row',
  marginTop: 20,
  marginRight: 15,
  marginBottom: 15,
  marginLeft: 15,
  flexBasis: 'auto',
  flexGrow: 1,
  flexShrink: 1,
  flexWrap: 'wrap',
  alignItems: 'center',
});
const ButtonWrapper = css({
  label: 'ButtonWrapper',
  flexDirection: 'row',
  flexGrow: 1,
  flexShrink: 1,
  flexBasis: '0%',
  justifyContent: 'flex-end',
  marginRight: 14,
});

const StandardModal = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'StandardModal'}|`}
      className={`views-block ${StandardModal1}`}
    >
      <TitleBar
        data-test-id={`StandardModal.TitleBar|`}
        text={props.title}
        onClick={props.toggleItem}
        isActive={true}
        className="views-block"
      />
      <div
        data-test-id={`StandardModal.OuterWrapper|`}
        className={`views-block ${OuterWrapper}`}
      >
        {props.isError ? (
          <ErrorIcon
            data-test-id={`StandardModal.ErrorIcon|`}
            className="views-block"
          />
        ) : null}
        {props.isWarning ? (
          <WarningIcon
            data-test-id={`StandardModal.WarningIcon|`}
            className="views-block"
          />
        ) : null}
        {props.isAbout ? (
          <Views
            data-test-id={`StandardModal.Views|`}
            height={40}
            width={40}
            className="views-block"
          />
        ) : null}
        <MessageWrapper
          data-test-id={`StandardModal.MessageWrapper|`}
          mainMessage={props.mainMessage}
          secondaryMessage={props.secondaryMessage}
          className="views-block"
        />
        <div
          data-test-id={`StandardModal.ButtonWrapper|`}
          className={`views-block ${ButtonWrapper}`}
        >
          {props.isError ? (
            <Button
              data-test-id={`StandardModal.Button|`}
              text="Cry yourself to sleep"
              name="showVideo"
              onClick={props.toggleItem}
              className="views-block"
            />
          ) : null}
          {props.isError ? (
            <Button
              data-test-id={`StandardModal.Button:1|`}
              text="Play again"
              onClick={props.playAgain}
              className="views-block"
            />
          ) : null}
          {!props.isError ? (
            <Button
              data-test-id={`StandardModal.Button:2|`}
              text="OK"
              onClick={props.toggleItem}
              className="views-block"
            />
          ) : null}
        </div>
      </div>
      {props.children}
    </div>
  );
};

export default StandardModal;
