import '../Fonts/DigitMono-400.js';
import React from 'react';
import { css } from 'emotion';

const CounterText1 = css({
  label: 'CounterText1',
  left: -1,
  fontFamily: 'DigitMono',
  fontWeight: 400,
  fontSize: 48,
  position: 'var(--position)',
  opacity: 'var(--opacity)',
});

const CounterText = props => {
  return (
    <span
      data-test-id={`${props['data-test-id'] || 'CounterText'}|`}
      style={{ '--position': props.position, '--opacity': props.opacity }}
      className={`views-block ${CounterText1}`}
    >
      {props.text}
    </span>
  );
};
CounterText.defaultProps = {
  position: 'absolute',
  opacity: 0.3,
  text: 888,
};
export default CounterText;
