/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import Cell from './Cell.view.logic';
import React from 'react';
import { css } from 'emotion';

const BoardColumn1 = css({ label: 'BoardColumn1', width: 28 });

const BoardColumn = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'BoardColumn'}|`}
      pass="item"
      className={`views-block ${BoardColumn1}`}
    >
      {Array.isArray(props.column) &&
        props.column.map((item, index) => (
          <Cell
            index={index}
            item={item}
            key={index}
            data-test-id={`BoardColumn.Cell|`}
            onClick={props.reveal}
            className="views-block"
          />
        ))}
      {props.children}
    </div>
  );
};

export default BoardColumn;
