import Board from './Board.view.js';
import Header from '../Header/Header.view.logic';
import Menu from '../Menu/Menu.view.logic';
import MenuBar from '../Menu/MenuBar.view.js';
import React from 'react';
import TitleBar from './TitleBar.view.js';
import { css } from 'emotion';

const Window1 = css({
  label: 'Window1',
  backgroundColor: '#C1C1C3',
  borderStyle: 'solid',
  borderWidth: 3,
  borderTopColor: '#fff',
  borderLeftColor: '#fff',
  borderBottomColor: '#000',
  borderRightColor: '#000',
});
const Border = css({
  label: 'Border',
  flexDirection: 'column',
  paddingTop: 12,
  paddingRight: 12,
  paddingBottom: 12,
  paddingLeft: 12,
  marginTop: 3,
  marginRight: 3,
  marginBottom: 3,
  marginLeft: 3,
  borderStyle: 'solid',
  borderWidth: 4,
  borderTopColor: '#fff',
  borderLeftColor: '#fff',
  borderBottomColor: '#939493',
  borderRightColor: '#939493',
});

const Window = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'Window'}|`}
      className={`views-block ${Window1}`}
    >
      <TitleBar
        data-test-id={`Window.TitleBar|`}
        hasIcon={true}
        isActive={false}
        text="Minesweeper"
        className="views-block"
      />
      <MenuBar
        data-test-id={`Window.MenuBar|`}
        toggleItem={props.toggleItem}
        className="views-block"
      />
      {props.showMenu ? (
        <Menu
          data-test-id={`Window.Menu|`}
          toggleItem={props.toggleItem}
          switchLevel={props.switchLevel}
          reset={props.reset}
          level={props.level}
          className="views-block"
        />
      ) : null}
      <div data-test-id={`Window.Border|`} className={`views-block ${Border}`}>
        <Header data-test-id={`Window.Header|`} className="views-block" />
        <Board
          data-test-id={`Window.Board|`}
          grid={props.grid}
          reveal={props.reveal}
          className="views-block"
        />
      </div>
      {props.children}
    </div>
  );
};

export default Window;
