        '--borderTopColor': `${props.isRevealed ? 'black' : 'white'}`,
        '--borderLeftWidth': `${props.isRevealed ? 1 : 2}px`,
        '--borderLeftStyle': `${props.isRevealed ? 'none' : 'solid'}`,
        '--borderLeftColor': `${props.isRevealed ? 'black' : 'white'}`,
        '--borderRightWidth': `${props.isRevealed ? 2 : 2}px`,
        '--borderRightColor': `${props.isRevealed ? 'black' : 'black'}`,
        '--borderRightStyle': `${props.isRevealed ? 'dotted' : 'solid'}`,
        '--borderBottomWidth': `${props.isRevealed ? 2 : 2}px`,
        '--borderBottomStyle': `${props.isRevealed ? 'dotted' : 'solid'}`,
        '--borderBottomColor': `${props.isRevealed ? 'black' : 'black'}`,
        '--borderTopLeftRadius': `${props.isRevealed ? 0 : 2}px`,
