import '../Fonts/LeviWindows-700.js';
import CloseButton from '../Buttons/CloseButton.view.logic';
import React from 'react';
import TitleBomb from '../Images/TitleBomb.view.js';
import { css } from 'emotion';

const TitleBar1 = css({
  label: 'TitleBar1',
  flexDirection: 'row',
  height: 28,
  width: '100%',
  backgroundColor: '#0016c0',
  paddingTop: 0,
  paddingRight: 2,
  paddingBottom: 0,
  paddingLeft: 6,
  lineHeight: '17px',
  borderWidth: 2,
  borderStyle: 'solid',
  borderColor: '#000',
  justifyContent: 'flex-start',
});
const Title = css({
  label: 'Title',
  fontFamily: 'LeviWindows',
  fontWeight: 700,
  fontSize: 30,
  letterSpacing: 1.7,
  color: '#FFF',
});

const TitleBar = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'TitleBar'}|`}
      className={`views-block ${TitleBar1}`}
    >
      {props.hasIcon ? (
        <TitleBomb
          data-test-id={`TitleBar.TitleBomb|`}
          className="views-block"
        />
      ) : null}
      <span data-test-id={`TitleBar.Title|`} className={`views-block ${Title}`}>
        {props.text}
      </span>
      <CloseButton
        data-test-id={`TitleBar.CloseButton|`}
        onClick={props.onClick}
        isActive={props.isActive}
        className="views-block"
      />
      {props.children}
    </div>
  );
};
TitleBar.defaultProps = { text: '' };
export default TitleBar;
