import '../Fonts/Arial-800.js';
import React from 'react';
import { css } from 'emotion';

const CloseButton1 = css({
  label: 'CloseButton1',
  flexDirection: 'row',
  height: 22,
  alignSelf: 'center',
  justifyContent: 'center',
  marginLeft: 'auto',
  backgroundColor: '#c0c7c8',
  borderBottomStyle: 'solid',
  borderBottomWidth: 2,
  borderRightWidth: 2,
  borderRightStyle: 'solid',
  borderLeftStyle: 'solid',
  borderLeftWidth: 2,
  borderTopWidth: 2,
  borderTopStyle: 'solid',
  width: 22,
  borderBottomColor: 'var(--borderBottomColor)',
  borderRightColor: 'var(--borderRightColor)',
  borderLeftColor: 'var(--borderLeftColor)',
  borderTopColor: 'var(--borderTopColor)',
});
const Text = css({
  label: 'Text',
  fontFamily: 'Arial',
  fontWeight: 800,
  zIndex: 1,
  color: 'var(--color)',
});

const CloseButton = props => {
  return (
    <button
      data-test-id={`${props['data-test-id'] || 'CloseButton'}|${
        props.isPressed ? 'isPressed|' : ''
      }`}
      onClick={props.onClick}
      onMouseDown={props.toggleState}
      onMouseUp={props.toggleState}
      style={{
        '--borderBottomColor': `${props.isPressed ? '#fff' : '#000'}`,
        '--borderRightColor': `${props.isPressed ? '#fff' : '#000'}`,
        '--borderLeftColor': `${props.isPressed ? '#000' : '#fff'}`,
        '--borderTopColor': `${props.isPressed ? '#000' : '#fff'}`,
      }}
      className={`views-block ${CloseButton1}`}
    >
      <span
        data-test-id={`CloseButton.Text|${props.isActive ? 'isActive|' : ''}`}
        style={{ '--color': `${props.isActive ? '#000' : '#7F7F7F'}` }}
        className={`views-block ${Text}`}
      >
        
      </span>
      {props.children}
    </button>
  );
};

export default CloseButton;
