/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import React from 'react';
import { css } from 'emotion';

const MessageWrapper1 = css({
  label: 'MessageWrapper1',
  flexDirection: 'column',
  marginLeft: 10,
  flexShrink: 1,
  lineHeight: 1.3,
  width: '85%',
});
const SecondaryMessage = css({ label: 'SecondaryMessage', marginTop: 14 });

const MessageWrapper = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'MessageWrapper'}|`}
      className={`views-block ${MessageWrapper1}`}
    >
      <span
        data-test-id={`MessageWrapper.MainMessage|`}
        className="views-block"
      >
        {props.mainMessage}
      </span>
      {props.secondaryMessage ? (
        <span
          data-test-id={`MessageWrapper.SecondaryMessage|`}
          className={`views-block ${SecondaryMessage}`}
        >
          {props.secondaryMessage}
        </span>
      ) : null}
      {props.children}
    </div>
  );
};
MessageWrapper.defaultProps = { mainMessage: '' };
export default MessageWrapper;
