/* eslint-disable jsx-a11y/accessible-emoji, no-unused-vars, no-dupe-keys */
import MenuItem from './MenuItem.view.logic';
import React from 'react';
import { css } from 'emotion';

const Menu1 = css({
  label: 'Menu1',
  position: 'absolute',
  backgroundColor: '#C1C1C3',
  zIndex: 2,
  top: 62,
  width: 160,
  paddingTop: 4,
  paddingBottom: 4,
  borderStyle: 'solid',
  borderWidth: 2,
  borderTopColor: '#fff',
  borderLeftColor: '#fff',
  borderBottomColor: '#000',
  borderRightColor: '#000',
});

const Menu = props => {
  return (
    <div
      data-test-id={`${props['data-test-id'] || 'Menu'}|`}
      className={`views-block ${Menu1}`}
    >
      {Array.isArray(props.from) &&
        props.from.map((item, index) => (
          <MenuItem
            index={index}
            {...item}
            key={index}
            data-test-id={`Menu.MenuItem|`}
            level={props.level}
            className="views-block"
          />
        ))}
      {props.children}
    </div>
  );
};

export default Menu;
